@tailwind base;
@tailwind components;
@tailwind utilities;
.lds-dual-ring {
    display: inline-block;
    width: 80px;
    height: 80px;
  }
  .lds-dual-ring:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid rgb(23, 22, 22);
    border-color: rgb(23, 22, 22) transparent rgb(23, 22, 22) transparent;
    animation: lds-dual-ring 1.2s linear infinite;
  }
  @keyframes lds-dual-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  #source{
    position: absolute;
    top:-3%;
    left: 0%;
  }